export default {
	data() {
		return {
			pageData: {}
		}
	},
	onLoad: function(ops) {
		if (ops.orderid != undefined) {
			this.orderid = ops.orderid;
		}
		this.getPage(ops.id);
	},
	methods: {
		getPage: function(id) {
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2c_product&a=show&ajax=1&id=" + id,
				data: {orderid: id},
				success: res => {
					this.pageData = res.data;
				}
			})
		}
	}
}
